import org.jetbrains.compose.web.css.selectors.CSSSelector
import org.jetbrains.compose.web.dom.Button
import org.jetbrains.compose.web.dom.Span
import org.jetbrains.compose.web.dom.Text
@Composable
fun eParishNavigation() {
    val ctx = rememberPageContext()
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .padding(8.px)
            .gap(14.px)
    ) {

        SidebarItem(
            icon = { FaBorderAll(size = IconSize.LG) },
            label = "Dashboard",
            onClick = {
               Global.selectedIndex.intValue = 0
            }
        )

        SidebarItem(
            icon = { FaUsers(size = IconSize.LG) },
            label = "Catholics",
            onClick = {
                Global.selectedIndex.intValue = 1

            }
        )

        SidebarItem(
            icon = { FaUser(size = IconSize.LG) },
            label = "Leaders",
            onClick = {
                Global.selectedIndex.intValue = 2
            }
        )

        SidebarItem(
            icon = { FaChartBar(size = IconSize.LG) },
            label = "Statistics",
            onClick = {
                Global.selectedIndex.intValue = 3
            }
        )

        SidebarItem(
            icon = { FaRightFromBracket(size = IconSize.LG) },
            label = "Logout",
            isDanger = true,
            onClick = {
                ctx.router.navigateTo(Routes.splash)
            }
        )
    }
}
@Composable
fun SidebarItem(
    icon: @Composable () -> Unit,
    label: String,
    isDanger: Boolean = false,
    onClick: () -> Unit
) {
    Button(
        attrs = Modifier
            .fillMaxWidth()
            .padding(topBottom = 4.px, leftRight = 4.px)
            .borderRadius(10.px)
            .background(Color.transparent)
            .border(0.px)
            .cursor(Cursor.Pointer)
            .onClick { onClick() }
            .toAttrs{
            }
    ) {
        Row(
            modifier = Modifier
                .fillMaxWidth()
                .padding(10.px)
                  .color(if (isDanger) eparishColors.red else eparishColors.text)
                .fontSize(18.px)
                .gap(16.px)
                .fontWeight(FontWeight.Bold),
            horizontalArrangement = Arrangement.Start,
            verticalAlignment = Alignment.CenterVertically
        ) {
            // ICON
            Row(
                modifier = Modifier.size(24.px), // consistent icon container size
                verticalAlignment = Alignment.CenterVertically
            ) {
                icon()
            }
            // LABEL
            Text(
                label
            )
        }
    }
}
